<!--词云图-->
<template>
  <div class="wordCloud">
    <div ref="echartsWorld" class="charts"></div>
  </div>
</template>

<script>
/**
 * @词云图
 * @author：guangning.li
 * @参数说明：
 * */
import echarts from 'echarts';
require('echarts-wordcloud');
import 'echarts/theme/macarons.js'

export default {
  name: "wordCloud",
  props: {
    wordCloudData: {
      type: Array,
      default: () => {
        return []
      }
    },
    wordCloudColor: {
      type: String,
      default: () => {
        return ''
      }
    },
    sizeData: {
      type: Array,
      default: () => {
        return [18, 24]
      }
    },
    position: {
      type: String,
      default: () => {
        return ''
      }
    }

  },
  data() {
    return {}
  },
  watch: {},
  created() {

  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$refs.echartsWorld);
      this.chart.setOption({
        series: [{
          type: 'wordCloud',
          left: 'center',
          top: 'center',
          width: '80%',
          height: '80%',
          right: null,
          bottom: null,
          sizeRange: [15, 35],
          rotationRange: [-0, 0],
          rotationStep: 45,
          gridSize: 30,
          drawOutOfBound: true,

          textStyle: {
            normal: {
              fontFamily: 'sans-serif',
              fontWeight: 'bold',
              // Color can be a callback function or a color string
              color: function () {
                // Random color
                return 'rgb(' + [
                  Math.round(Math.random() * 255),
                  Math.round(Math.random() * 255),
                  Math.round(Math.random() * 255)
                ].join(',') + ')';
              }
            },
            emphasis: {
              shadowBlur: 10,
              shadowColor: '#333'
            }
          },

          // Data is an array. Each array item must have name and value property.
          data: this.wordCloudData
        }]
      });
      // }
      this.chart.on('click', function (params) {
        window.console.log(params)
      });
    },

  }
}
</script>

<style scoped lang="scss">
.wordCloud {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>