<template>
    <div class="differenceServiceSupervision">
        <div class="header">
            <div class="selectNav">
                <el-select size="small" class="input_td" v-model="selectParams.dsjVal" clearable placeholder="请选择地市局" @change="dsjChange">
                    <el-option
                        v-for="item in selectParams.dsjOptions"
                        :key="item.ORG_CODE"
                        :label="item.ORG_NAME"
                        :value="item.ORG_CODE"
                    ></el-option>
                </el-select>
                <el-select v-model="selectParams.xqjVal" clearable placeholder="请选择县区局" class="input_td" size="small" @change="qxjChange">
                    <el-option
                        v-for="item in selectParams.xqjOptions"
                        :key="item.ORG_CODE"
                        :label="item.ORG_NAME"
                        :value="item.ORG_CODE"
                    >
                    </el-option>
                </el-select>
                <el-select size="small" class="input_td" v-model="selectParams.gdsVal" clearable placeholder="请选择供电所">
                    <el-option
                        v-for="item in selectParams.gdsOptions"
                        :key="item.ORG_CODE"
                        :label="item.ORG_NAME"
                        :value="item.ORG_CODE"
                    ></el-option>
                </el-select>
                <el-input
                    class="input_300"
                    placeholder="请输入停电工单号"
                    size="small"
                    v-model="tdgdhInput"
                    clearable>
                </el-input>
                <el-input
                    class="input_300"
                    placeholder="请输入用户编号"
                    size="small"
                    v-model="yhCodeInput"
                    clearable>
                </el-input>
                <el-button type="primary" size="small" icon="el-icon-search" @click="GetPlanPowerEventChecklist">查询</el-button>
                <el-button type="primary" size="small" @click="clickSuccessList">成功执行二次停电通知推送任务客户清单</el-button>
            </div>
        </div>
        <div class="content">
            <div class="title">当前在执行计划停电事件差异化服务客户清单</div>
            <div class="tableTd">
                <el-table
                    height="600"
                    v-loading="loading"
                    border
                    :data="tableData"
                    style="width: 100%"
                    :row-class-name="tableRowClassName">
                    <el-table-column
                        label="序号"
                        align="center"
                        type="index"
                        width="70">
                    </el-table-column>
                    <el-table-column
                        width="160"
                        prop="taskFlag"
                        label="停电敏感客户第二次短信通知任务标识">
                    </el-table-column>
                    <el-table-column
                        width="150"
                        prop="orderCode"
                        label="停电事件工单号">
                    </el-table-column>
                    <el-table-column
                        width="200"
                        prop="blackOut"
                        label="停电事件描述">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="tdjhkssj"
                        label="停电计划开始时间">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="tdjhjssj"
                        label="停电计划结束时间">
                    </el-table-column>
                    <el-table-column
                        width="100"
                        prop="sfycsystdcz"
                        label="是否已产生延时停电操作">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="updateDate"
                        label="数据更新时间">
                    </el-table-column>
                    <el-table-column
                        width="100"
                        prop="messageClientNum"
                        label="需第二次短信发送客户数">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="messageSendDate"
                        label="短信发送时间">
                    </el-table-column>
                    <el-table-column
                        width="150"
                        prop="yhbh"
                        label="用户编号">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="yhmc"
                        label="用户名称">
                    </el-table-column>
                    <el-table-column
                        width="150"
                        prop="yddz"
                        label="用电地址">
                    </el-table-column>
                    <el-table-column
                        width="150"
                        prop="dq"
                        label="地市局">
                    </el-table-column>
                    <el-table-column
                        width="150"
                        prop="gddw"
                        label="供电所">
                    </el-table-column>
                    <el-table-column
                        width="150"
                        prop="relationPhone"
                        label="联系号码">
                    </el-table-column>
                    <el-table-column
                        width="280"
                        prop="messageSendContent"
                        label="短信发送内容">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="messageSendDate"
                        label="短信发送时间">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="planPushSysDate"
                        label="计划推送至营销系统时间">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="160"
                        fixed="right"
                        label="操作">
                        <template slot-scope="scope">
                            <el-link type="success" @click="GetTdmgkhhx(scope.row)">查询画像</el-link>
                            <el-link type="primary" @click="openEditSendTime(scope.row)">配置短信数据推送时间</el-link>
                            <el-link type="danger" @click="DeleteMessageSendTask(scope.row)">删除短信发送任务</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="operatorUser"
                        width="100"
                        label="操作人">
                    </el-table-column>
                    <el-table-column
                        width="120"
                        prop="operatorDate"
                        label="操作时间">
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pages.pageNo"
                        :page-sizes="[8, 10, 20, 50]"
                        :page-size="pages.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pages.total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <el-dialog
            title="配置短信数据推送时间"
            :visible.sync="editTsTimeShow"
            width="40%"
            center>
            <div style="text-align: center;height: 100px">
                <el-date-picker
                    size="small"
                    style="width: 80%"
                    v-model="tsTime"
                    type="datetime"
                    value-format="yyyy-MM-dd hh:mm:ss"
                    placeholder="请选择时间"
                ></el-date-picker>
            </div>
            <span slot="footer" class="dialog-footer">
        <el-button @click="editTsTimeShow = false" size="small">取 消</el-button>
        <el-button type="primary" @click="setTsTime" size="small">确 定</el-button>
      </span>
        </el-dialog>

<!--        画像信息-->
        <el-dialog
            class="portraitInfo-dialog"
            :visible.sync="portraitInfoVisible"
            width="95%"
            top="5vh"
            :before-close="portraitInfoHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '客户用电日历画像信息' }}</span>
      </span>
            <div class="dialog-content">
                <div class="dialog-content-title">
                    <el-input
                        class="inputC"
                        placeholder="请输入用户编号"
                        v-model="portraitInfoQueryForm.yhbh"
                        size="small"
                        clearable
                    ></el-input>
                    <el-button size="small" type="primary" @click="GetTdmgkhhx">查询</el-button>
                </div>
                <div class="dialog-content-main">
                    <div class="recommend">
                        <div class="main-title">推荐建议</div>
                        <div class="text-item">
                            <div class="text">{{'当前具有电价档级升档风险,准备发送电量告警短信通知,短信通知内容为:'}}</div>
                            <div class="value">{{portraitInfoQueryForm.dxtznr}}</div>
                        </div>
                        <div class="text-item">
                            <div class="text">{{'短信发送时间为:'}}</div>
                            <div class="value">{{portraitInfoQueryForm.dxfssj}}</div>
                        </div>
                        <div class="text-item">
                            <div class="text">{{'联系方式为:'}}</div>
                            <div class="value">{{portraitInfoQueryForm.lxfs}}</div>
                        </div>
                    </div>
                    <div class="portrait-label">
                        <div class="main-content">
                            <div class="main-title">画像标签</div>
                            <div style="width: 100%;height: 500px">
                                <word-cloud :wordCloudData="wordCloudData" v-if="wordCloudDataVisible"></word-cloud>
                            </div>
                        </div>
                    </div>
                    <div class="calendar">
                        <div class="main-content">
                            <div class="td_title">停电敏感特征</div>
                            <el-table
                                :data="tableData1"
                                border
                                style="width: 100%">
                                <el-table-column prop="ydlb" label="用电类别"></el-table-column>
                                <el-table-column prop="dydj" label="电压等级"></el-table-column>
                                <el-table-column prop="ldcs" label="95598来电次数"></el-table-column>
                            </el-table>
                            <el-table
                                :data="tableData1"
                                border
                                style="width: 100%">
                                <el-table-column prop="tdzsc" label="停电总时长"></el-table-column>
                                <el-table-column prop="tdpjjgsc" label="停电平均间隔时长"></el-table-column>
                                <el-table-column prop="sffstdld" label="是否发生停电事件来电"></el-table-column>
                                <el-table-column prop="wltdmgd" label="未来15天停电敏感度"></el-table-column>
                            </el-table>
                            <el-divider></el-divider>
                            <div class="td_title">计划停电告警特征</div>
                            <el-table
                                :data="tableData2"
                                border
                                style="width: 100%">
                                <el-table-column prop="tdlxgdx" label="停电类型工单数"></el-table-column>
                                <el-table-column prop="tdcs" label="停电次数"></el-table-column>
                                <el-table-column prop="tdsjldxdb" label="停电事件来电相对比"></el-table-column>
                            </el-table>
                            <el-divider></el-divider>
                            <div class="td_title">二次短信通知特征</div>
                            <el-table
                                :data="tableData3"
                                border
                                style="width: 100%">
                                <el-table-column prop="fscftzby" label="发生过重复通知抱怨"></el-table-column>
                                <el-table-column prop="fswtzby" label="发生过无通知抱怨"></el-table-column>
                                <el-table-column prop="jhtdqstldzb" label="计划停电前3天来电咨询停电相对比"></el-table-column>
                            </el-table>
                            <el-table
                                :data="tableData3"
                                border
                                style="width: 100%">
                                <el-table-column prop="dqtddycdxsb" label="当前停电事件第一次短信发送失败"></el-table-column>
                                <el-table-column prop="lxfx" label="联系号码类型"></el-table-column>
                            </el-table>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>

<!--        //成功执行二次停电通知推送任务客户清单-->
        <el-dialog
            class="successList-dialog"
            :visible.sync="successListVisible"
            width="90%"
            top="5vh"
            :before-close="successListHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '成功执行二次停电通知推送任务客户清单查询' }}</span>
      </span>
            <div class="dialog-content">
                <div class="dialog-content-title" style="margin-bottom: 10px">
                    <el-select
                        size="small"
                        class="input_td"
                        v-model="selectParams.dsjVal"
                        clearable
                        placeholder="请选择地市局"
                        @change="dsjChange"
                    >
                        <el-option
                            v-for="item in selectParams.dsjOptions "
                            :key="item.ORG_CODE"
                            :label="item.ORG_NAME"
                            :value="item.ORG_CODE"
                        ></el-option>
                    </el-select>
                    <el-select
                        v-model="selectParams.xqjVal"
                        clearable
                        placeholder="请选择县区局"
                        class="input_td"
                        size="small"
                        @change="qxjChange"
                    >
                        <el-option
                            v-for="item in selectParams.xqjOptions"
                            :key="item.ORG_CODE"
                            :label="item.ORG_NAME"
                            :value="item.ORG_CODE"
                        ></el-option>
                    </el-select>
                    <el-select
                        size="small"
                        class="input_td"
                        v-model="selectParams.gdsVal"
                        clearable
                        placeholder="请选择供电所"
                    >
                        <el-option
                            v-for="item in selectParams.gdsOptions"
                            :key="item.ORG_CODE"
                            :label="item.ORG_NAME"
                            :value="item.ORG_CODE"
                        ></el-option>
                    </el-select>
                    <el-date-picker
                        size="small"
                        class="input_td"
                        v-model="successListQueryForm.time"
                        type="date"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        placeholder="请选择推送日期"
                    ></el-date-picker>
                    <el-input
                        class="input_td"
                        placeholder="请输入用户编号"
                        v-model="selectValue"
                        size="small"
                        clearable
                    ></el-input>
                    <el-button size="small" type="primary" @click="getSuccessListData">查询</el-button>
                </div>
                <div class="dialog-content-main" style="height: 600px">
                    <bdaTable
                        :tableData="successListData"
                        :isPage="true"
                        :height="600"
                        :pageConfig="successListPageConfig"
                        @changePage="changePage"
                    ></bdaTable>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
  import Interface1 from '@/base/mixin/power-sensitive-users';
  import Interface2 from '@/base/mixin/common-api';
  import Interface3 from '@/base/mixin/electricity-behavior';
  import wordCloud from "../../electricityBehavior/components/wordCloud";
  import bdaTable from '@/components/bda-table/bda-table'
  export default {
    name: "differenceServiceSupervision",
    mixins: [Interface1,Interface2,Interface3],
    components:{
      wordCloud,
      bdaTable
    },
    props:{

    },
    data(){
      return {
        //导航模块变量
        selectParams:{
          dsjOptions:[],
          dsjVal:'',
          xqjOptions:[],
          xqjVal:'',
          gdsOptions: [],
          gdsVal:''
        },
        yhCodeInput:'',
        tdgdhInput:'',
        //表格
        pages:{
          pageNo:1,
          pageSize:10,
          total:11,
        },
        tableData:[],
        loading:false,
        //表格操作
        editTsTimeShow:false,
        tsTime:'',
        code:'',
        // 画像信息
        portraitInfoVisible: false,
        portraitInfoQueryForm: {
          yhbh: "",
          dxtznr: '',
          dxfssj: "",
          lxfs: ""
        },
        tableData1:[],//停电敏感特征
        tableData2:[],//计划停电告警特征
        tableData3:[],//二次短信通知特征

        //画像数据
        wordCloudData: [],
        wordCloudDataVisible: false,

        //成功执行二次停电通知推送任务客户清单
        successListVisible:false,
        successListQueryForm: {
          time: "",
          yhbh: "",
          current: 1,
          size: 10,
        },
        gddwOptions: [],
        gdsOptions: [],
        xqjOptions: [],
        selectValue: '',
        successListData: {},
        successListPageConfig: {
          name: "successList",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
      }
    },
    mounted() {
      this.GetPowerAreaCode()//地区
      this.GetPlanPowerEventChecklist()
    },
    methods:{
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.GetPlanPowerEventChecklist()
        window.console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.GetPlanPowerEventChecklist()
        window.console.log(`当前页: ${val}`);
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex%2===0) {
          return 'success-row';
        }
        return '';
      },
      //获取供电单位编码--地市局
      GetPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
          // "orgCode":"0306" //上一级编码
        }).then(res => {
          this.selectParams.dsjOptions = res
        })
      },
      //获取区县局--区县局
      GetQxjPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
          "orgCode":this.selectParams.dsjVal //上一级编码
        }).then(res => {
          this.selectParams.xqjOptions = res
        })
      },
      //获取供电所
      GetGdsPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
          "orgCode":this.selectParams.xqjVal //上一级编码
        }).then(res => {
          this.selectParams.gdsOptions = res
        })
      },
      //地市局选择
      dsjChange(){
        this.selectParams.gdsVal = ''
        this.selectParams.gdsOptions =[]
        this.selectParams.xqjVal=''
        this.selectParams.xqjOptions =[]
        this.GetQxjPowerAreaCode()
      },
      //区县局选择
      qxjChange(){
        this.selectParams.gdjVal = ''
        this.selectParams.gdsOptions =[]
        this.GetGdsPowerAreaCode()
      },

      //停电敏感客户明细列表
      GetPlanPowerEventChecklist(){
        this.getPlanPowerEventChecklist({
          "dsj":this.selectParams.dsjVal,
          "xqj":this.selectParams.xqjVal,
          "gds":this.selectParams.gdsVal,
          "yhbh":this.yhCodeInput,
          "tdgdh":this.tdgdhInput,
          "size":this.pages.pageSize,
          "current":this.pages.pageNo
        }).then(res=>{
          this.pages.total = res.total
          this.tableData = res.list
        })
      },
      //删除短信发送任务
      DeleteMessageSendTask(row){
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteMessageSendTask({
            code:row.code,
            yhbh:row.yhbh,
            tdgdh:row.orderCode,
          }).then(()=>{
            this.GetPlanPowerEventChecklist()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //配置短信数据推送时间打开弹窗
      openEditSendTime(row){
        this.code = row.code
        this.tsTime = row.planPushSysDate||''
        this.editTsTimeShow = true;
      },
      //配置短信数据推送时间
      setTsTime(){
        this.updateMessagePushDate({
          code:this.code,
          planPushDate:this.tsTime,
        }).then(()=>{
          this.editTsTimeShow = false;
          this.GetPlanPowerEventChecklist()
        })
      },
      //画像信息
      // 关闭画像信息
      portraitInfoHandleClose() {
        this.portraitInfoVisible = false
      },
      //获取停电敏感客户画像
      GetTdmgkhhx(row=null){
        if (row.yhbh){
          this.portraitInfoQueryForm.yhbh  = row.yhbh
        }
        this.portraitInfoVisible=true
        this.getWordCloudData()
        this.GetRecommendations()
        this.getTdmgkhhx({
          yhbh:this.portraitInfoQueryForm.yhbh,
        }).then((res)=>{
          this.tableData1 = [res.tmNpmisTdmgjsjgkhxxT]
          this.tableData2 = [res.tmNpmisTdmgjhgjkhxxT]
          this.tableData3 = [res.tmNpmisTdmgdrcdxtzsfjsT]
          window.console.log(res)
        })
      },
      //获取画像信息
      getWordCloudData() {
        this.wordCloudDataVisible = false
        this.getUserPortraitInfo({
          yhbh: this.portraitInfoQueryForm.yhbh
          // yhbh: '32123'
        }).then(res => {
          let wordCloudData = []
          if (res && res.length !== 0) {
            res.forEach(e => {
              wordCloudData.push({
                name: e,
                value: parseInt(Math.random() * 100)
              })
            })
          }
          this.wordCloudData = wordCloudData
          this.wordCloudDataVisible = true
        })
      },
      //推荐建议
      GetRecommendations() {
        this.getRecommendations2({
          "yhbh": this.portraitInfoQueryForm.yhbh
        }).then(res => {
          this.portraitInfoQueryForm.dxtznr = res.MESSAGE_SEND_CONTENT
          this.portraitInfoQueryForm.lxfs = res.LXDH
          this.portraitInfoQueryForm.dxfssj = res.SMS_SEND_TIME
        })
      },

      //成功执行二次停电通知推送任务客户清单
      // 关闭成功
      successListHandleClose() {
        this.successListVisible = false
      },
      getSuccessListData() {
        let columnNameData = [
          { prop: 'id', label: '停电敏感客户第二次短信通知任务标识' },
          { prop: 'sendToYxTimePlan', label: '计划推送至营销系统时间' },
          { prop: 'sendToYxTime', label: '推送至营销系统时间' },
          { prop: 'tdgdh', label: '停电工单号' },
          { prop: 'yhbh', label: '用户编号' ,},
          { prop: 'yhmc', label: '用户名称' },
          { prop: 'yddz', label: '用电地址',width: 200 },
          { prop: 'dsj', label: '地市局' },
          { prop: 'xqj', label: '县区局' },
          { prop: 'gds', label: '供电所' },
          { prop: 'lxdh', label: '联系号码' },
          { prop: 'content', label: '短信发送内容', width: 300 },
          { prop: 'smsSendTime', label: '短信发送时间' }
        ]
        this.getPlanPowerEventChecklist({
          "dsj": this.selectParams.dsjVal,
          "xqj": this.selectParams.xqjVal,
          "gds": this.selectParams.gdsVal,
          "page": this.successListQueryForm.current,
          "size": this.successListQueryForm.size,
          "yhbh": this.selectValue,
          "tsrq": this.successListQueryForm.time,
        }).then(res => {
          this.loading = false
          this.successListPageConfig.total = res.total
          this.successListData = {
            tableData: [
              ...res.list
            ],
            headerColor: '#1899fa',
            title: "告警清单",
            name: 'successList',
            size: this.successListQueryForm.size,
            current: this.successListQueryForm.current,
            total: this.successListPageConfig.total,
            columnNameData
          }
        })

      },
      //改变分页
      changePage(data) {
        if (data.name === 'successList') {
          this.successListQueryForm.size = data.size
          this.successListQueryForm.current = data.current
          this.getSuccessListData()
        }
      },
      // 成功执行电量告警通知推送任务客户清单查询
      clickSuccessList() {
        this.successListVisible = true
        this.getSuccessListData()
      },
    }
  }
</script>

